@import "./variables.scss";
@import "./mixin.scss";

.product {
  width: 200px;
  height: 403px;
  border-radius: 5px;
  border: 1px solid $grey-color;
  padding: 15px 14px;
  display: flex;
  flex-direction: column;
  transition: 1s;

  &__btn {
    margin-top: 20px;
    @include flex-align-items;
    justify-content: center;
    width: 170px;
    height: 40px;
    border-radius: 5px;
    background-color: $active-color;
    text-transform: uppercase;
    text-decoration: none;
    color: $default-color;
    font-weight: 700;
    font-size: 14px;
    transition: 1s;
  }

  &:hover {
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.07);
  }

  &__btn:hover {
    background-color: $default-color;
    border: 1px solid $active-color;
    color: $active-color;
  }

  &__image {
    align-self: center;
    width: 166px;
    height: 156px;
    border: solid 3px transparent;
    border-radius: 5px 5px 0 0;
    padding: 12px 0 4px;
    margin-bottom: 38px;
    transition: transform 1s;
  }

  &__image:hover {
    transform: scale(1.2);
    border-color: $grey-color;
  }

  &__name {
    line-height: 15px;
    font-size: 12px;
    font-weight: 500;
    margin: 0 0 5px;
  }

  &__review {
    @include flex-align-items;
    justify-content: space-between;
    margin-top: 15px;
  }

  &__feedbacks {
    text-decoration: none;
    color: $text-color;
    align-items: center;
    line-height: 13px;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    margin-top: 20px;
  }

  &__price-value {
    font-size: 16px;
    font-weight: 700;
    line-height: 19px;
  }
}
